html,
body,
#root,
.app {
  min-height: 100vh;
  box-sizing: border-box;
}

.app {
  display: flex;
  padding: 50px 10px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #4219c3;
  background: -webkit-linear-gradient(top left, #4219c3, #424e67);
  background: -moz-linear-gradient(top left, #4219c3, #424e67);
  background: linear-gradient(to bottom right, #4219c3, #424e67);
}
